/*
vars
*/

$black: #010000;
$white: #ffffff;
$lightGrey: #e6e6e6;
$darkGrey: #333;
$red: #ff1755;

/*
fonts

regular 400
regular 400 italics
bold 700
bold 700 italics

*/

/*
mixins
*/
@mixin translateY($e){
    -webkit-transform:translateY($e);
    -moz-transform: translateY($e);
    transform: translateY($e);
}

@mixin transition($e){
    -webkit-transition:$e;
    -moz-transition: $e;
    transition: $e;
}

body{
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    color: $black;
    font-weight: 400;
    font-style: normal;
}

a{
    text-decoration: none;
    @include transition(.4s ease-in-out);
    cursor: pointer;

    &:hover{
        color:red;
        @include transition(.4s ease-in-out);
    }
}



.container-fluid{
    line-height: 24px;

}

