@import "_config.scss";



header{
    position: relative;
    display:block;
    width:100%;
    background: $black;
    color: $white; 
    height: 90px;  
}

.logo{
    position: absolute;
    top: 50%; left: 15px;
    @include translateY(-50%);
}

nav,
nav ul,
nav ul li{
    margin: 0;
    padding: 0;

}

nav{
    position: absolute;
    top: 60%; right: 15px;
    @include translateY(-50%);

    ul li {
        list-style-type: none;
        display: inline-block;
        margin-right: 20px;

        &.last{
            margin: 0;

        }

        a{
            font-size: 19px;
            line-height: normal;
          
        }
    }
}

body, html, #root, .app, .container {
    width: 100%;
    height: 100%;
  }
  
  
  .container {
    font-size: 28px;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #282c34;
  }
  
  .loginForm {
   box-sizing: border-box;
   width: 100%;
   max-width: 400px;
   padding-left: 200px;
   padding-right: 200px;
  }
  
  .inputField {
   padding-top: 16px;
   padding-right: 1000px; 
  }
  
  .input {
   box-sizing: border-box;
   outline: none;
   border: solid 2px #1189de;
   border-radius: 4px;
   color: #292929;
   width: 100%;
   padding: 12px;
   font-size: 14px;
   background: rgba(255, 255, 255, 1);
  }
  
  .submitButton {
   padding-top: 16px;
   padding-right: 1000px;
  }
  
  
  .btn {
   width: 100%;
   min-width: 280px;
   color: #565656;
   padding: 12px;
   font-size: 14px;
   font-weight: bold;
   border: solid 2px #1189de;
   border-radius: 4px;
   background: #fff;
   cursor: pointer;
  }